*,
*:after,
*:before {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  width: 100%;
  padding: 0;
  font-family: 'Inter', sans-serif;
  color: #000;
  line-height: 1.42;
}
img {
  max-width: 100%;
}
a {
  text-decoration: none;
}
button {
  border: none;
}

.app {
  position: relative;
  width: 100%;
  height: 100%;
}

.button {
    width: 280px;
    height: 37px;
    background: #3369F3;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 8px;
    cursor: pointer;

    font-weight: 500;
  font-size: 13px;
  line-height: 13px;
  text-align: center;
  color: #FFFFFF;
}

.card {
  position: absolute;
  left: 50%;
  top: 50%;

  width: 340px;
  padding: 50px 30px 30px;
  background: #FFFFFF;
  box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.14);
  border-radius: 12px;
  transform: translate3d(-50%, -50%, 0);

  &__title {
    margin-bottom: 50px;
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    text-align: center;
    color: #1E1E1E;
  }
}

.form__link {
  display: block;
  text-align: center;
}
